<template>
	<view class="container">
	 
	  <view>
	   <view class="cph">车牌号为：{{cph}}</view>
	   <canvas canvas-id="canvas" :onclick="click()" style="background:#fff;width: 200px;height: 200px;"/>
	   </view>
	  </view>
	
</template>

<script> 
    import QRCode from '../../utils/weapp-qrcode.js'
	export default {
		data() {
			return {
				cph:null,
				postUrl:null,
				czbh:""
			}
		},
		onLoad(options) {
		    console.log(options.cph);
			this.cph=options.cph;
			this.czbh=options.czbh
		  },
		methods: {
			click(){
				var qrcode = new QRCode('canvas', {
					  text: this.czbh,
					  width: 200,
					  height: 200,
					  padding: 12, // 生成二维码四周自动留边宽度，不传入默认为0
					  correctLevel: QRCode.CorrectLevel.H, // 二维码可辨识度
					  callback: (res) => {
						console.log(res.path)
					  }
					})
			}
		}
	}
</script>

<style>
 .container{
	  margin-left: 90px;
	 margin-top: 100px;
 }
 .cph{
 	 margin-left: 15px;
  }
</style>
